<template>
  <div>
    <div class="video-list lightest-bg">
      <div
        class="myVideo"
        v-for="(item, index) in (videoList || '').slice(0, 2)"
        :key="index"
      >
        <img
          :src="item.images ? item.images.appdetailsmall : defaultPcBackImg"
          class="title"
        />
        <!-- <img src="../../../assets/img/back.png" class="title" v-else> -->
        <a href="javascript:void(0)" @click="goVideo(item, index)">
          <span
            class="iconfont icon-bofang lightest-text"
            id="play-picture"
          ></span>
        </a>
        <p class="text-1 dark-text">{{ item ? item.title : "" }}</p>
        <p class="text-2 light-text">{{ item ? item.categoryTitle : "" }}</p>
      </div>

      <videoTemplate :videoList="videoList"></videoTemplate>
    </div>
  </div>
</template>
<script>
import videoTemplate from "../../../components/video-template";
export default {
  props: ["videoList"],
  data() {
    return {
      imgURL: require("@/assets/img/back.png")
    };
  },
  methods: {
    goVideo(item, index) {
      var _this = this;
      //  console.log(item)
      this.$router.push({
        path: "/video/details"
      });
      this.$store.commit("setVideo", item);
      this.$store.commit("setVideoList", this.videoList);
    }
  },
  mounted() {},
  components: {
    videoTemplate
  }
};
</script>
